#nav-menu{
    height: 80px;
    width: 100%;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    display: flex;
position: fixed;
margin-top: -5px;
background-color: white;
top: 0;
left: 0;
z-index: 1; 
}

#nav-content>a:hover {
    color: red;
  }

#nav-menu>div>a{
    text-decoration: none;
    color: black;
    font-size:large;
    padding: 0px 40px;
}
#nav-menu>div:nth-child(2){
    margin-left: 500px;
   margin-top: 25px;
   font-size: larger;
}
#nav-menu>div:nth-child(1){
    margin-left: 50px;
    color: black;
}
#home{
 background-image: url(../Img/banner.png);
 margin-top: 20px;
background-size: 100%;
 height: 800px;
 padding-top: 50px;
}
#resume-button-2 {
    background-color: red;
    color: white;
    padding: 10px 20px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    margin-top: 500px;
    margin-right: 420px;
  }
  #about{
    padding-top: 100px;
