 #skills{
  padding-top: 60px;
  color: rgb(49, 49, 47);
 }
 #skill-card{
  display: grid;
  grid-template-columns: repeat(3,1fr);
 padding-bottom: 100px;
 }
 #skill-card>div{
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
   margin: 0px 30px;
   border-radius: 20px;
 }
 #skill-card>div>img{
   padding: 10px 30px;
 }
 #skill-card>div:nth-child(2)>h3,#skill-card>div:nth-child(3)>h3{
 margin-top: 100px;
 padding: 0px 10px;
}
#skill-card>div:nth-child(2)>img,#skill-card>div:nth-child(3)>img{
 margin-top: 50px;
 }
 #github{
  display: grid;
  grid-template-columns: repeat(2,1fr);
 }
#git-section{
  color: rgb(49, 49, 47);
  font-size:30px;
}
 #github>div:nth-child(1){
margin-left: 300px;
 }
 #github>div:nth-child(2){
